"use client";

import { ChevronDown, ChevronUp } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";
import React, { useState, useEffect, useContext } from "react";

export default function EnergyWrapper() {
  const [points, setPoints] = useState(10);
  const [used, setUsed] = useState(0);

  function downPoints() {
    setUsed(used + 1);
  }

  function upPoints() {
    setUsed(used - 1);
  }

  useEffect(() => {
    setPoints(
      5 + 5 * +(document.getElementById("level") as HTMLInputElement).value
    );
    console.log(points);
    console.log(used);
  });

  return (
    <fieldset className="grid relative gap-6 rounded-lg border p-4">
      <legend className="-ml-1 px-1 text-sm font-medium">Energy</legend>
      <span className="absolute -top-6 right-3 p-1 bg-white">
        Points Remaining: {points - used}
      </span>
      <div className="flex flex-wrap">
        <EnergyCard
          title="Body"
          total={points - used}
          downPoints={downPoints}
          upPoints={upPoints}
        />
        <EnergyCard
          title="Senses"
          total={points - used}
          downPoints={downPoints}
          upPoints={upPoints}
        />
        <EnergyCard
          title="Endurance"
          total={points - used}
          downPoints={downPoints}
          upPoints={upPoints}
        />
        <EnergyCard
          title="Will"
          total={points - used}
          downPoints={downPoints}
          upPoints={upPoints}
        />
        <EnergyCard
          title="Mind"
          total={points - used}
          downPoints={downPoints}
          upPoints={upPoints}
        />
        <div className="grow shrink basis-1/5 min-w-32 max-w-56 mr-2 mb-2"></div>
        <div className="grow shrink basis-1/5 min-w-32 max-w-56 mr-2 mb-2"></div>
        <div className="grow shrink basis-1/5 min-w-32 max-w-56 mr-2 mb-2"></div>
      </div>
    </fieldset>
  );
}

export function EnergyCard({
  title,
  total,
  downPoints,
  upPoints,
}: {
  title: string;
  total: number;
  downPoints: Function;
  upPoints: Function;
}) {
  const [value, setValue] = React.useState(10);
  const [die, setDie] = React.useState("d0");

  function handleUp() {
    if (value < 60 && total > 0) {
      setValue(value + 1);
      downPoints();
    }
  }

  function handleDown() {
    if (value > 10) {
      setValue(value - 1);
      upPoints();
    }
  }

  useEffect(() => {
    if (value < 20) {
      setDie("d0");
    } else if (value < 30) {
      setDie("d4");
    } else if (value < 40) {
      setDie("d6");
    } else if (value < 50) {
      setDie("d8");
    } else if (value < 60) {
      setDie("d10");
    } else {
      setDie("d12");
    }
  }, [value]);

  return (
    <Card className="grid gap-1 text-center shadow basis-1/5 min-w-32 max-w-56 mr-2 mb-2 grow shrink">
      <CardHeader className="p-1 pt-2 pb-0 font-semibold">{title}</CardHeader>
      <Separator className="" />
      <CardContent className="flex items-center space-x-2 p-2">
        <div className="grow grid grid-cols-2 h-full">
          <p className="col-span-2 p-1 pb-2 text-sm font-extralight">Energy</p>
          <p className="self-center text-right">
            {title === "Endurance" ? 2 * value : value}
          </p>
          <div className="grid grid-cols-1 justify-items-center">
            <Button
              variant="ghost"
              size="icon"
              className="h-4 w-4"
              onClick={handleUp}
            >
              <ChevronUp className="h-3 w-3" />
              <span className="sr-only">Up</span>
            </Button>
            <Button
              variant="ghost"
              size="icon"
              className="h-4 w-4"
              onClick={handleDown}
            >
              <ChevronDown className="h-3 w-3" />
              <span className="sr-only">Down</span>
            </Button>
          </div>
        </div>
        <Separator orientation="vertical" />
        <div className="grow grid grid-cols-1 h-full">
          <div className="p-1 pb-2 text-sm font-extralight">Die</div>
          <div>{die}</div>
        </div>
      </CardContent>
    </Card>
  );
}
